import s from "./Dialogs.module.css";
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Messages/Message";
import AddNewMessageFormRedux from "./DialogForm";
import Preloader from "../common/Preloader/Preloader";

const Dialogs = (props) => {
  const { dialogsPage, addNewMessage } = props;

  if (!dialogsPage || !dialogsPage.dialogsData || !dialogsPage.messagesData) {
    return <Preloader />;
  }

  const state = dialogsPage;

  const dialogsElements = state.dialogsData.map((dialog) => (
    <DialogItem
      name={dialog.name}
      id={dialog.id}
      avatar={dialog.avatar}
      key={dialog.id}
    />
  ));

  const messagesElements = state.messagesData.map((m) => (
    <Message message={m.message} key={m.id} />
  ));

  const onAddNewMessage = (formData) => {
    addNewMessage(formData.newMessageBody);
  };

  return (
    <div className={s.dialogs}>
      <div className={s.dialogsItems}>{dialogsElements}</div>
      <div className={s.messages}>
        <div>{messagesElements}</div>
        <div className={s.textArea}>
          <AddNewMessageFormRedux onSubmit={onAddNewMessage} />
        </div>
      </div>
    </div>
  );
};

export default Dialogs;
